.product__slider{height:621px;position:relative;background:radial-gradient(circle, rgba(0, 212, 255, 0) 2%, rgb(255, 255, 255) 100%)}.product__slider:before,.product__slider:after{background:linear-gradient(0deg, #fff 0%, rgba(255, 255, 255, 0) 100%);content:"";display:block;position:absolute;width:100%;height:20%;pointer-events:none;z-index:20}.product__slider:before{top:0;transform:rotate(180deg)}.product__slider:after{bottom:0}@media screen and (max-width: 640px){.product__slider{height:502px;width:69px !important}}@media screen and (max-width: 420px){.product__slider{height:402px}}@media screen and (max-width: 320px){.product__slider{height:302px;width:43px !important}}.product__slider>div{height:570px !important;width:89px;overflow:hidden;margin-top:26px;position:relative}@media screen and (max-width: 640px){.product__slider>div{width:69px;height:451px !important;margin-top:21px}}@media screen and (max-width: 420px){.product__slider>div{height:375px !important}}@media screen and (max-width: 320px){.product__slider>div{height:277px !important;width:43px;margin-top:16px}}.product__slider>div>div{flex-direction:column;gap:20px 0}@media screen and (max-width: 640px){.product__slider>div>div{gap:15px 0}}@media screen and (max-width: 320px){.product__slider>div>div{gap:10px 0}}.product__slider>div>div>div>div{height:98px}@media screen and (max-width: 640px){.product__slider>div>div>div>div{height:78px}}@media screen and (max-width: 320px){.product__slider>div>div>div>div{height:46px}}.product__slider img{height:100%;width:100%;display:block;cursor:pointer}.product__slider button{position:absolute;background:linear-gradient(331deg, #0b3f37 0%, #cb8d62 96.96%);transform:rotate(-90deg);padding:7px 10px;font-size:0;z-index:100}.product__slider button:after{content:"❮";font-size:24px;color:#fff}.product__slider button:first-child{transform:rotate(90deg);top:-8px;left:29px}.product__slider button:last-child{transform:rotate(-90deg);bottom:-8px;left:29px}.product__slider button:disabled{opacity:.5}@media screen and (max-width: 640px){.product__slider button:first-child,.product__slider button:last-child{left:20px}}@media screen and (max-width: 320px){.product__slider button{padding:4px 5px 3.5px}.product__slider button:after{font-size:10px}.product__slider button:first-child,.product__slider button:last-child{left:15px}.product__slider button:first-child{top:-3px}.product__slider button:last-child{bottom:-3px}}