.catalog__controls {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #ebebeb;

  @media screen and (max-width: 1110px) {
    flex-wrap: wrap;
  }

  @media screen and (max-width: 769px) {
    margin-bottom: 20px;
  }

  @media screen and (max-width: 460px) {
    gap: 20px;
  }

  @media screen and (max-width: 320px) {
    padding-bottom: 20px;
  }

  button {
    background-position: center center;
    width: 25%;
    min-height: 147px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    position: relative;
    transition: 0.3s;
    box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.1);

    @media screen and (max-width: 1110px) {
      width: calc(50% - 15px);
    }

    @media screen and (max-width: 460px) {
      width: calc(50% - 10px);
      min-height: 198px;
    }

    @media screen and (max-width: 375px) {
      min-height: 148px;
    }

    &:before {
      content: "";
      position: absolute;
      display: block;
      background: linear-gradient(
        180deg,
        transparent 23%,
        rgba(7, 44, 38, 0.5) 100%
      );
      width: 100%;
      height: 100%;
      bottom: 0;
      z-index: 2;
      transition: 0.1s;
      opacity: 1;
    }

    &:hover:before {
      opacity: 0;
      transition: 0.3s;
      pointer-events: none;
    }

    &:after {
      content: "";
      position: absolute;
      display: block;
      background: linear-gradient(
        180deg,
        rgba(203, 141, 98, 0) 0%,
        #cb8d62 96.96%
      );
      width: 100%;
      height: 0;
      bottom: 0;
      z-index: 2;
      transition: 0.3s;
      pointer-events: none;
    }

    &:hover:after {
      height: 100%;
      transition: 0.3s;
      pointer-events: auto;
    }

    &:hover {
      box-shadow: 0 0 70px 0 rgba(0, 0, 0, 0.2);
      transition: 0.3s;
    }

    & > span {
      font-weight: 600;
      text-align: center;
      color: #fff;
      font-size: 20px;
      padding-bottom: 20px;
      position: relative;
      z-index: 3;

      @media screen and (max-width: 640px) {
        font-size: 18px;
      }

      @media screen and (max-width: 550px) {
        font-size: 16px;
      }

      @media screen and (max-width: 460px) {
        font-size: 14px;
      }

      @media screen and (max-width: 320px) {
        font-size: 12px;
      }
    }
  }

  & > button:nth-child(1) {
    background-image: url("../../../assets/CATALOG/controls/1.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media screen and (max-width: 460px) {
      background-image: url("../../../assets/CATALOG/controls/1-s.jpg");
    }
  }

  & > button:nth-child(2) {
    background-image: url("../../../assets/CATALOG/controls/2.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media screen and (max-width: 460px) {
      background-image: url("../../../assets/CATALOG/controls/2-s.jpg");
    }
  }

  & > button:nth-child(3) {
    background-image: url("../../../assets/CATALOG/controls/3.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media screen and (max-width: 460px) {
      background-image: url("../../../assets/CATALOG/controls/3-s.jpg");
    }
  }

  & > button:nth-child(4) {
    background-image: url("../../../assets/CATALOG/controls/4.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media screen and (max-width: 460px) {
      background-image: url("../../../assets/CATALOG/controls/4-s.jpg");
    }
  }
}
